import React,{Component} from 'react';
import {variables} from './Variables.js';

export class Customer extends Component{

    constructor(props){
        super(props);

        this.state={
            customers:[],
            modalTitle:"",
            CustomerId:0,
            FirstName:"",
            LastName:"",
            UserName:"",
            EmailAddress:"",
            DateOfBirth:"",
            Age:0,
            DateCreated:"",
            DateEdited:"",
            IsDeleted:"",
        }
    }

    refreshList(){

        fetch(variables.API_URL+'customer')
        .then(response=>response.json())
        .then(data=>{
            this.setState({customers:data});
        });
    }

    componentDidMount(){
        this.refreshList();
    }
    
    changeFirstName =(e)=>{
        this.setState({FirstName:e.target.value});
    }
    changeLastName =(e)=>{
        this.setState({LastName:e.target.value});
    }
    changeUserName =(e)=>{
        this.setState({UserName:e.target.value});
    }
    changeEmailAddress =(e)=>{
        this.setState({EmailAddress:e.target.value});
    }
    changeDateOfBirth =(e)=>{
        this.setState({DateOfBirth:e.target.value});
    }
    changeAge =(e)=>{
        this.setState({Age:e.target.value});
    }
    changeDateCreated =(e)=>{
        this.setState({DateCreated:e.target.value});
    }
    changeDateEdited =(e)=>{
        this.setState({DateEdited:e.target.value});
    }
    changeIsDeleted =(e)=>{
        this.setState({IsDeleted:e.target.value});
    }

    addClick(){
        this.setState({
            modalTitle:"Add Customer",
            CustomerId:0,
            FirstName:"",
            LastName:"",
            UserName:"",
            EmailAddress:"",
            DateOfBirth:"",
            Age:0,
            DateCreated:"",
            DateEdited:"",
            IsDeleted:""            
        });
    }
    editClick(emp){
        this.setState({
            modalTitle:"Edit Customer",
            CustomerId:emp.CustomerId,
            FirstName:emp.FirstName,
            LastName:emp.LastName,
            UserName:emp.UserName,
            EmailAddress:emp.EmailAddress,
            DateOfBirth:emp.DateOfBirth,
            Age:emp.Age,
            DateCreated:emp.DateCreated,
            DateEdited:emp.DateEdited,
            IsDeleted:emp.IsDeleted
        });
    }

    createClick(){
        fetch(variables.API_URL+'customer',{
            method:'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
            FirstName:this.state.FirstName,
            LastName:this.state.LastName,
            UserName:this.state.UserName,
            EmailAddress:this.state.EmailAddress,
            DateOfBirth:this.state.DateOfBirth,
            Age:this.state.Age,
            DateCreated:this.state.DateCreated,
            DateEdited:this.state.DateEdited,
            IsDeleted:this.state.IsDeleted
            })
        })
        .then(res=>res.json())
        .then((result)=>{
            alert(result);
            this.refreshList();
        },(error)=>{
            alert('Failed');
        })
    }


    updateClick(){
        fetch(variables.API_URL+'customer',{
            method:'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                CustomerId:this.state.CustomerId,
                FirstName:this.state.FirstName,
                LastName:this.state.LastName,
                UserName:this.state.UserName,
                EmailAddress:this.state.EmailAddress,
                DateOfBirth:this.state.DateOfBirth,
                Age:this.state.Age,
                DateCreated:this.state.DateCreated,
                DateEdited:this.state.DateEdited,
                IsDeleted:this.state.IsDeleted
            })
        })
        .then(res=>res.json())
        .then((result)=>{
            alert(result);
            this.refreshList();
        },(error)=>{
            alert('Failed');
        })
    }

    deleteClick(id){
        if(window.confirm('Are you sure?')){
        fetch(variables.API_URL+'customer/'+id,{
            method:'DELETE',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json'
            }
        })
        .then(res=>res.json())
        .then((result)=>{
            alert(result);
            this.refreshList();
        },(error)=>{
            alert('Failed');
        })
        }
    }

    render(){
        const {
            customers,
            modalTitle,
            CustomerId,
            FirstName,
            LastName,
            UserName,
            EmailAddress,
            DateOfBirth,
            Age,
            DateCreated,
            DateEdited,
            IsDeleted
        }=this.state;

        return(
<div>

    <button type="button"
    className="btn btn-primary m-2 float-end"
    data-bs-toggle="modal"
    data-bs-target="#exampleModal"
    onClick={()=>this.addClick()}>
        Add Customer
    </button>
    <table className="table table-striped">
    <thead>
    <tr>
        <th>
            CustomerId
        </th>
        <th>
            FirstName
        </th>
        <th>
            LastName
        </th>
        <th>
            UserName
        </th>
        <th>
            EmailAddress
        </th>
        <th>
            DateOfBirth
        </th>
        <th>
            Age
        </th>
        <th>
            DateCreated
        </th>
        <th>
            DateEdited
        </th>
        <th>
            IsDeleted
        </th>
    </tr>
    </thead>
    <tbody>
        {customers.map(emp=>
            <tr key={emp.CustomerId}>
                <td>{emp.FirstName}</td>
                <td>{emp.LastName}</td>
                <td>{emp.UserName}</td>
                <td>{emp.EmailAddress}</td>
                <td>{emp.DateOfBirth}</td>
                <td>{emp.Age}</td>
                <td>{emp.DateCreated}</td>
                <td>{emp.DateEdited}</td>
                <td>{emp.IsDeleted}</td>
                <td>
                <button type="button"
                className="btn btn-light mr-1"
                data-bs-toggle="modal"
                data-bs-target="#exampleModal"
                onClick={()=>this.editClick(emp)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square" viewBox="0 0 16 16">
                    <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                    <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                    </svg>
                </button>

                <button type="button"
                className="btn btn-light mr-1"
                onClick={()=>this.deleteClick(emp.CustomerId)}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash-fill" viewBox="0 0 16 16">
                    <path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z"/>
                    </svg>
                </button>

                </td>
            </tr>
            )}
    </tbody>
    </table>

<div className="modal fade" id="exampleModal" tabIndex="-1" aria-hidden="true">
<div className="modal-dialog modal-lg modal-dialog-centered">
<div className="modal-content">
   <div className="modal-header">
       <h5 className="modal-title">{modalTitle}</h5>
       <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
       ></button>
   </div>

   <div className="modal-body">
        <div className="d-flex flex-row bd-highlight mb-3">
        
            <div className="p-2 w-50 bd-highlight">
        
                <div className="input-group mb-3">
                    <span className="input-group-text">First Name</span>
                    <input type="text" className="form-control"
                    value={FirstName}
                    onChange={this.changeFirstName}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Last Name</span>
                    <input type="date" className="form-control"
                    value={LastName}
                    onChange={this.changeLastName}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">User Name</span>
                    <input type="text" className="form-control"
                    value={UserName}
                    onChange={this.changeUserName}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Email Address</span>
                    <input type="date" className="form-control"
                    value={EmailAddress}
                    onChange={this.changeEmailAddress}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Date of Birth</span>
                    <input type="text" className="form-control"
                    value={DateOfBirth}
                    onChange={this.changeDateOfBirth}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Age</span>
                    <input type="text" className="form-control"
                    value={Age}
                    onChange={this.changeAge}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Date Created</span>
                    <input type="date" className="form-control"
                    value={DateCreated}
                    onChange={this.changeDateCreated}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Date Edited</span>
                    <input type="text" className="form-control"
                    value={DateEdited}
                    onChange={this.changeDateEdited}/>
                </div>

                <div className="input-group mb-3">
                    <span className="input-group-text">Is Deleted</span>
                    <input type="date" className="form-control"
                    value={IsDeleted}
                    onChange={this.changeIsDeleted}/>
                </div>
            
            </div>
        
        </div>

        {CustomerId===0?
            <button type="button"
            className="btn btn-primary float-start"
            onClick={()=>this.createClick()}
            >Create</button>
            :null}

            {CustomerId!==0?
            <button type="button"
            className="btn btn-primary float-start"
            onClick={()=>this.updateClick()}
            >Update</button>
            :null}
   </div>

</div>
</div> 
</div>


</div>
        )
    }
}